import React, {
  FunctionComponent,
  Suspense,
  useEffect,
  useMemo,
  useState,
} from "react";
import SwipeableViews from "react-swipeable-views";
import { useHistory, useLocation } from "react-router-dom";
import clsx from "clsx";

import { ProgressiveModal, Select, SelectOption } from "components";
import { coinsManager, useCoins } from "hooks";
import { useGetLocale } from "providers";

import { ModalsProps } from "config/routes";
import { walletContentList } from "./config";

import Template from "pages/ModalPages/componentsProgressive/Template";
import { Button, DialogHeader } from "../../componentsProgressive";

import css from "./style.module.scss";
import { isDeepStrictEqual } from "util";
import { isEqual } from "utils";

const Wallet: FunctionComponent<ModalsProps> = ({ isOpen }) => {
  const intl = useGetLocale();
  const history = useHistory();
  const [selectedIndex, setSelectedIndex] = useState(0);

  const { coinsList, selectedCoin, coinsState } = useCoins();
  const [currency, setCurrency] = useState<string>(selectedCoin?.coin || "");

  useEffect(() => {
    if (!selectedCoin?.coin) {
      return;
    }

    setCurrency(selectedCoin?.coin);
  }, [selectedCoin?.coin]);

  useEffect(() => {
    const { search } = history.location;
    if (!search) return;
    const params = new URLSearchParams(search);
    const record = params.get("records");

    if (record) {
      setCurrency(record);
      setSelectedIndex(2); // to records section
      params.delete("records");
      history.replace({ ...history.location, search: params.toString() });
    }
  }, [history.location.search]);

  useEffect(() => {
    if (!isEqual(coinsState, {}) && !coinsState[currency]) {
      setCurrency(selectedCoin?.coin || "GEM");
    }
  }, [coinsState, currency]);

  const currenciesOptions = useMemo<SelectOption[]>(() => {
    return coinsList.map<SelectOption>((c) => {
      if (selectedCoin?.coin === c.coin) {
        return {
          value: c.coin,
          label: selectedCoin.balance,
          icon: c.image,
        };
      }

      return {
        value: c.coin,
        label: c.balance,
        icon: c.image,
      };
    });
  }, [coinsList, selectedCoin]);

  const tabsMap = useMemo(() => {
    return walletContentList.map(
      ({ contentComponent, dataIndex, IconComponent, id, tabTitleLocale }) => {
        return {
          label: (
            <React.Fragment key={id}>
              <div className={css[`${dataIndex}Icon`]}>
                <IconComponent />
              </div>
              <span className={css.label}>
                {intl.formatMessage({ id: tabTitleLocale })}
              </span>
            </React.Fragment>
          ),
          Component: contentComponent,
        };
      },
    );
  }, [intl]);

  return (
    <ProgressiveModal
      className={css.modalRoot}
      open={isOpen}
      width={530}
      aria-labelledby="wallet-dialog-title"
      aria-describedby="wallet-dialog-description">
      <DialogHeader title={intl.formatMessage({ id: "wallet.title" })} />

      <Template>
        <Select
          value={currency}
          options={currenciesOptions}
          onChange={(coin) => {
            if (typeof coin === "string") {
              setCurrency(coin);
              coinsManager.changeSelectedCoin(coin);
            }
          }}
          className={css.select}
        />

        <div className={css.dialogActions}>
          {tabsMap.map(({ label }, key) => (
            <Button
              key={key}
              className={clsx(css.button, [
                selectedIndex !== key && css.transparentButton,
              ])}
              onClick={() => setSelectedIndex(key)}
              transparent={selectedIndex !== key}>
              {label}
            </Button>
          ))}
        </div>

        <SwipeableViews disabled index={selectedIndex}>
          {tabsMap.map(({ Component }, key) => (
            <div className={css.tabs} key={key}>
              {selectedIndex === key && (
                <Suspense fallback={null}>
                  <Component key={key} currency={currency} />
                </Suspense>
              )}
            </div>
          ))}
        </SwipeableViews>
      </Template>
    </ProgressiveModal>
  );
};

export default Wallet;
